import DateRange from "../date-range";
import Dropdown, { TYPES } from "../dropdown";

const submitFormHandler = (e) => {
  e.preventDefault();
  e.stopPropagation();

  /* eslint-disable no-console */
  console.log("submit form");
};

const priceFormat = (price) => price
  .toString()
  .split("")
  .reverse()
  .map((ch, index) => ((index + 1) % 3 === 0 ? ` ${ch}` : ch))
  .reverse()
  .join("");

export default class RoomDetailForm {
  constructor({ classId }) {
    this.classId = classId;

    this.numberOfDaysClass = "js-room-detail-form__number-of-days";
    this.costPerDaysClass = "js-room-detail-form__cost-per-days-cost";
    this.resultCostPerDayClass = "js-room-detail-form__cost-per-days-result-cost";

    this.discountSumClass = "js-room-detail-form__discount-sum";
    this.additionalFeeClass = "js-room-detail-form__additional-fee-sum";
    this.resultSumClass = "js-room-detail-form__result-value";

    this.topCostClass = "js-room-detail-form__header-cost-value";
  }

  start() {
    this._domInit();

    this._eventsInit();

    this.state = {
      roomCost: parseInt(this.topCostEl.innerHTML, 10),
      discountSum: parseInt(this.discountSumEl.innerHTML, 10),
      additionalFee: parseInt(this.additionalFeeEl.innerHTML, 10),
      numberOfDays: 0,
      result: 0,
    };

    this._formatCosts();

    this.dropdown = new Dropdown({
      classId: `${this.classId}-dropdown`,
      type: TYPES.guests,
      defaultMessage: "Сколько гостей",
    });
    this.dropdown.start();

    const dateRange = new DateRange({
      classId: `${this.classId}-date-range`,
      onCalendarOpen: this._dateRangeCalendarOpenHandler.bind(this),
      onSelected: this._dateRangeSelectHandler.bind(this),
      onClean: this._dateRangeCleanHandler.bind(this),
    });
    dateRange.start();
  }

  _domInit() {
    this.rootEl = document.querySelector(`.${this.classId}`);
    if (this.rootEl === null) {
      throw new Error(`ResultRoomRange error: not found element with class .${this.classId}`);
    }

    this.formEl = this.rootEl.querySelector(".room-detail-form__form");

    this.numberOfDaysEl = this.rootEl.querySelector(`.${this.numberOfDaysClass}`);
    this.costPerDaysEl = this.rootEl.querySelector(`.${this.costPerDaysClass}`);
    this.resultCostPerDayEl = this.rootEl.querySelector(`.${this.resultCostPerDayClass}`);

    this.discountSumEl = this.rootEl.querySelector(`.${this.discountSumClass}`);
    this.additionalFeeEl = this.rootEl.querySelector(`.${this.additionalFeeClass}`);
    this.resultSumEl = this.rootEl.querySelector(`.${this.resultSumClass}`);

    this.topCostEl = this.rootEl
      .querySelector(`.${this.topCostClass}`);
  }

  _eventsInit() {
    this.formEl.addEventListener("submit", submitFormHandler);
  }

  _setState(newState) {
    this.state = Object.assign(this.state, newState);
    this._render();
  }

  _render() {
    this.numberOfDaysEl.innerHTML = this.state.numberOfDays;
    this.resultCostPerDayEl.innerHTML = priceFormat(this._calculateCostPerDays());

    this.resultSumEl.innerHTML = this._calculateResultCost();
  }

  _formatCosts() {
    this.topCostEl.innerHTML = priceFormat(this.topCostEl.innerHTML);
    this.discountSumEl.innerHTML = priceFormat(this.discountSumEl.innerHTML);
    this.resultCostPerDayEl.innerHTML = priceFormat(this.resultCostPerDayEl.innerHTML);
    this.costPerDaysEl.innerHTML = priceFormat(this.state.roomCost);
  }

  _dateRangeCalendarOpenHandler() {
    this.dropdown.hideDropdown();
  }

  _dateRangeSelectHandler({ dateLeft, dateRight }) {
    const numberOfDays = (
      new Date(dateRight).getTime() - new Date(dateLeft).getTime()
    ) / (1000 * 60 * 60 * 24);

    this._setState({ numberOfDays });
  }

  _dateRangeCleanHandler() {
    this._setState({
      numberOfDays: 0,
    });
  }

  _calculateCostPerDays() {
    return this.state.numberOfDays * this.state.roomCost;
  }

  _calculateResultCost() {
    return this.state.numberOfDays === 0 ? 0 : priceFormat(
      this._calculateCostPerDays() - this.state.discountSum + this.state.additionalFee,
    );
  }
}
